import type { AppRouter } from '@server/app-router/app.router'
import {
	TRPCClientError,
	createTRPCProxyClient,
	createTRPCReact,
	httpBatchLink,
} from '@trpc/react-query'

import packageJson from '../../package.json' assert { type: 'json' }

export const trpcReact = createTRPCReact<AppRouter>()

const links = [
	httpBatchLink({
		url: `${import.meta.env.VITE_BACKEND_API_URL}/trpc`,
		fetch(url, options) {
			return fetch(url, {
				...options,
				credentials: 'include',
				headers: {
					'x-form-solver-version': packageJson.version,
					'x-locale': navigator.language,
				},
			})
		},
	}),
]

export const trpcClient = trpcReact.createClient({
	links,
})

export const trpc = createTRPCProxyClient<AppRouter>({
	links,
})

export function isTRPCClientError(
	cause: unknown,
): cause is TRPCClientError<AppRouter> {
	return cause instanceof TRPCClientError
}
