import { z } from 'zod'

export const SUPPORTED_LANGUAGES = ['en', 'id'] as const

export type Language = (typeof SUPPORTED_LANGUAGES)[number]

export type LooseLanguage = Exclude<string, Language> & Language

export const SafeLanguageSchema = z.enum(SUPPORTED_LANGUAGES).catch('en')

export const parseLanguage = (locale: unknown): Language =>
	SafeLanguageSchema.parse(locale)

export const LanguageSchema = z.enum(SUPPORTED_LANGUAGES)
