import { z } from 'zod'

const OptionSchema = z.object({
	text: z.string(),
	id: z.string(),
})

const BaseQuestionSchema = z.object({
	title: z.string(),
	id: z.string(),
})

export type BaseQuestion = z.infer<typeof BaseQuestionSchema>

const questionTypesWithOptions = ['radio', 'checkbox'] as const
export const QuestionTypesWithOptionsSchema = z.enum(questionTypesWithOptions)
export type QuestionTypeWithOptions = z.infer<
	typeof QuestionTypesWithOptionsSchema
>

export const QuestionWithOptionsSchema = BaseQuestionSchema.extend({
	type: QuestionTypesWithOptionsSchema,
	options: z.array(OptionSchema).min(1),
})

const questionTypesWithTextAnswer = ['input', 'textarea'] as const
const QuestionWithTextAnswer = BaseQuestionSchema.extend({
	type: z.enum(questionTypesWithTextAnswer),
})

export const QuestionSchema = z.discriminatedUnion('type', [
	QuestionWithOptionsSchema,
	QuestionWithTextAnswer,
])

export const QUESTION_TYPES = [
	...questionTypesWithOptions,
	...questionTypesWithTextAnswer,
] as const

export type Option = z.infer<typeof OptionSchema>
export type QuestionWithOptions = z.infer<typeof QuestionWithOptionsSchema>
export type QuestionWithTextAnswer = z.infer<typeof QuestionWithTextAnswer>
export type Question = z.infer<typeof QuestionSchema>
